import {
  Icon,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import React from 'react';
import { Link } from 'gatsby';
import type { MdxNode } from '../nav';

export interface LinkMenuProps {
  node: MdxNode;
}

const LinkMenu: React.FC<LinkMenuProps> = (props) => {
  const { node } = props;

  return (
    <ListItemButton
      component={Link}
      to={node.fields.slug || '404'}
      activeClassName="Mui-selected"
      partiallyActive={node.fields.slug === '/' ? false : true}
    >
      <ListItemIcon>
        <Icon>{node.frontmatter?.icon || 'article'}</Icon>
      </ListItemIcon>
      <ListItemText primary={node.frontmatter?.title} />
    </ListItemButton>
  );
};

export default LinkMenu;
